.back-to-top {
  display: none;

  &.visible {
    opacity: 1;
    visibility: visible;
  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    background-color: #00c4cc; /*     */
    transition: background-color 0.3s ease; /*    */
  }

  &::before {
    width: 1px; /*    */
    height: 12px; /*   */
    transform: rotate(45deg); /*      */
    top: 16px; /*  */
    left: 20px; /*  */
  }

  &::after {
    width: 1px; /*    */
    height: 12px; /*   */
    transform: rotate(-45deg); /*      */
    top: 16px; /*  */
    left: 27px; /*  */
  }

  /*     */
  &:hover {
    background-color: #00c4cc; /*   */
    border-color: #00c4cc; /*   */
  }

  /*      */
  &:hover::before,
  &:hover::after {
    background-color: white; /*   */
  }

  @include on-tablet {
    position: fixed; /*   */
    bottom: 240px;
    right: 35px;
    width: 50px; /*   */
    height: 50px; /*   */
    background-color: transparent;
    border: 2px solid #00c4cc; /*   */
    border-radius: 50%; /*   */
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0; /*    */
    visibility: hidden; /*    */
    transition:
      background-color 0.3s ease,
      border-color 0.3s ease,
      opacity 0.3s ease,
      visibility 0.3s ease; /*   */
  }

  @include on-desktoplittle {
    bottom: 225px;
    right: 110px;
  }

  @include on-desktopmiddle {
    bottom: 330px;
    right: 235px;
  }
}

